@import url(https://fonts.googleapis.com/css?family=Share+Tech+Mono);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		-webkit-animation: App-logo-spin infinite 20s linear;
		        animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}
@font-face {
	font-family: "Digital";
	src: url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot");
	src: url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot?#iefix") format("embedded-opentype"), url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff2") format("woff2"),
		url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff") format("woff"), url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.ttf") format("truetype"),
		url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.svg#Digital-7") format("svg");
}

.formulaScreen {
	min-height: 20px;
	font-family: digital;
	font-size: 20px;
	color: orange;
	background-color: #ffcccc40;
	text-align: right;
	vertical-align: text-top;
	line-height: 20px;
	overflow-wrap: break-word;
	word-wrap: break-word;
}
.button-calc-row {
  background:radial-gradient(circle, rgba(52,58,64,1) 0%, rgba(255,255,255,1) 100%)
}
.button-calc {

	background-color: #292d3e;

	border-radius: 4px;
	padding: 0 15px;
	cursor: pointer;
	height: 32px;
	font-size: 14px;
	-webkit-transition: all 0.2s ease-in-out;
	transition: all 0.2s ease-in-out;
}
.button-calc:hover {
	-webkit-transform: scale(0.9);
	        transform: scale(0.9);
}
/* .button-calc:hover {
	box-shadow: 1px 1px #343a40, 2px 2px #343a40, 3px 3px #343a40;
} */

button:focus {
	box-shadow: none !important;
	outline: none !important;
}

.upper-screen {
	background: antiquewhite;
	padding: 13px;
}

.btn {
	font-weight: bolder !important;
}

.outputScreen {
	font-size: 29px;
	font-family: digital;
	color: white;
	background-color: #343a40;
	text-align: center;
	line-height: 35px;
}

